label {
  display: flex;
  cursor: pointer;
  font-weight: 500;
  position: relative;
  overflow: hidden;
  margin-bottom: 0.375em;
  /* Accessible outline */
  /* Remove comment to use */
  /*
    &:focus-within {
        outline: .125em solid $primary-color;
    }
  */
  input {
    position: absolute;
    left: -9999px;
    &:checked + span {
      // background-color: mix(#fff, #2d3074, 84%);
      &:before {
        box-shadow: inset 0 0 0 0.4375em #2d3074;
      }
    }
  }
  span {
    display: flex;
    align-items: center;
    // padding: 0.375em 0.75em 0.375em 0.375em;
    border-radius: 99em; // or something higher...
    transition: 0.25s ease;
    // &:hover {
    //   background-color: mix(#fff, #2d3074, 84%);
    // }
    &:before {
      display: flex;
      flex-shrink: 0;
      content: "";
      // background-color: #fff;
      width: 18px;
      height: 18px;
      border-radius: 50%;
      transition: 0.25s ease;
      box-shadow: inset 0 0 0 0.125em #2d3074;
      position: relative;
      // margin-right: 8px;
      @media (max-width: 950px) {
        top: -3px;
      }
    }
  }
}
