@import "./../variables/_mixins";

.service {
  &__title {
    padding-top: 140px;
    max-width: 596px;
    width: 100%;
    margin: 0 auto;
    color: #fff;
  }
  &__subtitle {
    @include text(24px, 700, 25px, #ffffff);
    font-family: "Nunito Sans";
    margin-bottom: 10px;
    white-space: nowrap;
  }
  &__text,
  &__subtitle {
    color: #fff;
    margin-left: 20px;
  }
  &__block {
    max-width: 193px;
    width: 100%;
  }
  &__wrap {
    display: flex;
    align-items: center;
    margin: 10px;
  }
  &__wrap:first-child,
  &__wrap:nth-child(2) {
    @media all and (max-width: 600px) {
      margin-bottom: 68px;
    }
  }

  &__wrapper {
    display: flex;
    justify-content: space-around;
    margin-top: 31px;
    padding-bottom: 94px;
    @media all and (max-width: 900px) {
      flex-wrap: wrap;
    }
  }
}
