@import "./../variables/_mixins";

.reviews {
  &__title {
    padding: 146px 0 41px;
    color: #2d3074;
  }
  &__wrapper {
    margin: 0 20px;
    width: 400px;
    width: 100%;
    background: #55b2f3;
    border-radius: 42px;
    
    @media all and (max-width: 560px) {
      margin: 0 9px;
      // min-width: 212px;
    }
    @media all and (max-width: 361px) {
      // min-width: min-content;
      

    }
  }
  &__text_top,
  &__text_bottom,
  &__span {
    color: #ffffff;
  }
  &__wrap {
    padding: 20px 20px;
    height: 280px;
    @media all and (max-width: 560px) {
      padding: 26px 20px;
      height: 240px;
    }
    @media all and (max-width: 1334px) {
      padding: 20px 25px;
      height: 290px;
    }
    @media all and (max-width: 550px) {
      padding: 20px 25px;
      height: 230px;
    }
  }
  &__text_top {
    margin-bottom: 15px;
  }
  &__text_bottom {
    margin: 15px 0;
    @media all and (max-width: 560px) {
      @include text(11px, 600, 14px, #ffffff);
    }
  }
  &__span {
    @include text(10px, 400, 10px, #ffffff);
  }
  &__slider {
    // display: flex;
    // justify-content: space-between;
    // height: 295px;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    // max-width: 100%;
    width: 100%;
    // height: 277px;
  }
}
.slider__wrapper {
  max-width: 100%;
  margin: 0 80px;
  // height: 278px;
  // height: 100%;
  @media all and (max-width: 560px) {
    margin: 0 20px;
  }
  @media all and (max-width: 530px) {
    margin: 0 0px;
  }
}
