@import "./../variables/_mixins";

.request {
  &__text {
    text-align: center;
    @include text(18px, 400, 22px, #ffffff);
    @media (max-width: 950px) {
      @include text(16px, 400, 22px, #24265b);
      margin-bottom: 16px;
      margin-top: 5px;
    }
  }
  
  &__wrap {
    max-width: 400px;
    width: 100%;
  }
  &__title {
    padding-top: 140px;
    color: #ffffff;
    @media all and (max-width: 950px) {
      padding-top: 81px;
      color: #24265b;
    }
  }
  
  &__input {
    font-family: "Nunito Sans";
    padding: 10px;
    @include text(16px, 400, 22px, #ffffff);
    border: 1.5px solid #ffffff;
    border-radius: 42px;
    max-width: 296px;
    width: 100%;
    height: 50px;
    background: #2271c5;
    color: #121212;
    text-align: center;
    margin-right: 10px;
    @media all and (max-width: 950px) {
      background: rgba(255, 255, 255, 0.7);
      border: 0.912046px solid #272674;
      box-shadow: 0px 1.82409px 1.82409px 1.82409px rgba(204, 204, 204, 0.1);
      border-radius: 38.3059px;
      margin: 10px;
      max-width: 360px;
      text-align: left;
      padding-left: 36px;
    }
  }
  &__input_white {
    color: #ffffff;
    @media all and (max-width: 950px) {
      color: #121212;
    }
  }
  &__form__text {
    color: #ffffff;
    @include text(15px, 400, 14px, #ffffff);
    display: flex;
    gap: 15px;
  }
  &__form_second,
  &__form_second-mobile {
    display: flex;
    justify-content: center;
    margin: 0 auto;
    padding-top: 32px;
    @media all and (max-width: 950px) {
      padding: 16px 0 19px;
      max-width: 300px;
      width: 100%;
    }
  }
  &__btn {
    max-width: 250px;
    width: 100%;
    height: 50px;
    background: #f1a039;
    padding-left: 22px;
    position: relative;
    font-weight: 900;
    padding-top: 4px;
    &:hover::before {
      background: url(./../images/dropHover1.svg) no-repeat center;
    }
    &:hover {
      background: #272674;
    }
    @media all and (max-width: 950px) {
      max-width: 230px;
      background: #272674;
      &:hover::before {
        background: url(./../images/dropHover2.svg) no-repeat center;
      }
      &:hover {
        background: #f1a039;
      }
    }
  }
  &__form__text-mobile {
    @include text(12px, 400, 16px, #272674);
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
  }

  &__btn::before {
    content: "";
    position: absolute;
    background: url(./../images/dropOrange.svg) no-repeat center;
    width: 40px;
    height: 40px;
    top: 4px;
    left: 4px;
    @media all and (max-width: 950px) {
      background: url(./../images/drop.svg) no-repeat center;
    }
  }
  &__forms-mobile {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__form_first,
  &__form-mobile {
    max-width: 916px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    padding-top: 36px;
    @media all and (max-width: 950px) {
      padding-top: 20px;
      flex-direction: column;
      align-items: center;
    }
  }
  &__forms_descktop,
  .descktop {
    display: block;
    @media all and (max-width: 950px) {
      display: none;
    }
  }
  &__form-mobile {
    display: none;
  }
  &__form-mobile.active {
    @media all and (max-width: 950px) {
      display: flex;
    }
  }

  .mobile {
    display: none;
    @media all and (max-width: 950px) {
      display: flex;
    }
  }

  &__radioButton,
  &__radioButton-mobile {
    margin-right: 14px;
  }
}
input::placeholder {
  opacity: 0.5;
  @include text(16px, 400, 22px, #ffffff);
  font-family: "Nunito Sans";
  @media all and (max-width: 950px) {
    color: #c4c4c4;
  }
}

.mobile {
  display: flex;
  flex-direction: column;
  align-items: center;
  &__wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 7px;
    & span {
      @include text(18px, 700, 24px, #8e98a8);
      @media (max-width: 400px) {
        @include text(14px, 700, 24px, #8e98a8);
      }
    }
    @media (max-width: 500px) {
      gap: 5px;
    }
  }

  &__BTNtext {
    @include text(18px, 700, 24px, #8e98a8);
    background: inherit;
    cursor: pointer;
    transition: all 0.3s ease 0s;
    @media (max-width: 400px) {
      @include text(14px, 700, 24px, #8e98a8);
    }
  }
  &__BTNtext.clicked {
    @include text(18px, 700, 24px, #272674);
    @media (max-width: 400px) {
      @include text(14px, 700, 24px, #272674);
    }
  }
}
.request__form_second {
  display: flex;
}
.mobile__wrap {
  font-family: "Nunito Sans";
}

