@import "./../variables/_mixins";

.qualities {
  &__title {
    color: #ffffff;
    // margin-top: 30px;
    padding: 200px 0 68px;
    @media all and (max-width: 900px) {
      padding: 120px 0 68px;
    }
    @media all and (max-width: 600px) {
      padding: 161px 0 54px;
    }
  }
  &__block {
    max-width: 290px;
    width: 100%;
  }
  &__wrap {
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;
    @media all and (max-width: 1300px) {
      margin: 10px;
    }
    @media all and (max-width: 620px) {
      margin: 0px;
    }
  }
  &__wrap:not(:first-child) {
    @media all and (max-width: 636px) {
      padding-top: 50px;
    }
  }
  &__blocks {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @media all and (max-width: 1300px) {
      justify-content: space-around;
    }
    @media all and (max-width: 636px) {
      padding-top: 50px;
    }
  }
  &__subtitle {
    font-family: "Nunito Sans";
    @include text(24px, 700, 32px, #ffffff);
    margin-top: 10px;
    @media all and (max-width: 636px) {
      @include text(18px, 700, 25px, #ffffff);
    }
  }
  &__text {
    font-family: "Nunito Sans";
    margin-top: 16px;
    @include text(16px, 400, 22px, #ffffff);
    @media all and (max-width: 636px) {
      @include text(16px, 400, 22px, #ffffff);
    }
  }
  &__wrapper {
    margin: 0 auto;
    padding-top: 80px;
    padding-bottom: 120px;
    @media all and (max-width: 1280px) {
      // padding-top: 200px;
      margin-top: 220px;
    }
    @media all and (max-width: 951px) {
      margin-top: 0px;
    }
    @media all and (max-width: 640px) {
      padding-bottom: 220px;
    }
  }
}
