.icon-menu {
  display: block;
  position: relative;
  width: 34px;
  height: 22px;
  cursor: pointer;
  z-index: 12; }
  .icon-menu span, .icon-menu::before, .icon-menu::after {
    content: "";
    transition: all 0.3s ease 0s;
    right: 0;
    position: absolute;
    width: 100%;
    height: 4px;
    background-color: #2d3074; }
  .icon-menu::before {
    top: 0; }
  .icon-menu::after {
    bottom: 0; }
  .icon-menu span {
    top: calc(50% - 1.5px); }
  .menu-open .icon-menu span {
    width: 0; }
  .menu-open .icon-menu::before {
    top: calc(50% - 2px);
    transform: rotate(-45deg); }
